.login-wrapper{
  height: 100%;
  width: 100%;
  background: url(../../static/img/1.2567c4d.jpg) no-repeat center center;
  background-size: cover;
  position: relative;
}
.logo{
  text-align: center;
  margin-bottom: 1rem;
}
.login-box{
  position: absolute;
  width: 6rem;
  height: 8rem;
  left: 50%;
  top: 50%;
  margin-left: -3rem;
  margin-top: -4rem;
}
.input-box{
  width: 6rem;
  height: 0.8rem;
  border-bottom:1px solid #ccc;
  margin-bottom: 0.3rem;
  /* border-radius: 4px; */
}
.input-box input{
  background:none;  
  outline:none;  
  border:0px;
  height: 0.8rem;
  color: #f6f6f6
}
.input-box label{
  display: inline-block;
  margin-left: 0.1rem;
  width: 0.6rem
}
.input-box .iconfont{
  color: #f6f6f6;
  font-size: 0.36rem;
}
.input-box.verification img{
  width: 2.8rem;
  display: inline-block;
  vertical-align: top
}
.input-box.verification input{
  width: 2.3rem;
}
.submit{
  text-align: center;
  width: 6rem;
  height: 0.6rem;
  background-color: #409EFF;
  border-radius: 0.25rem;
  margin: 0.5rem auto 0;
  font-size: 0.36rem;
  line-height: 0.6rem;
  color: #fff;
}

.index-wrapper{
    height: 100%;
    overflow: scroll
}
.index-wrapper .iconfont{
        line-height: 0.5rem;
        font-size: 0.36rem;
}
.tabbar{
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        text-align: center;
        background-color: #fafafa;
}
.tabbar .tabbar-item{
        -webkit-box-flex: 1;
            -ms-flex: 1;
                flex: 1;
        text-align: center;
        padding: 0.1rem 0;
}
.tabbar .tabbar-item.selected{
        background-color: #409EFF;
        color: #fff;
}

.circle{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.circle .circle-item{
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1
}

.chart-header{
    height: 0.6rem;
    line-height: 0.6rem;
    font-size: 0.28rem;
    text-indent: 2em;
}



























